import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';

import * as Yup from 'yup';
import { Formik } from 'formik';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import CardWithBack from '../../../components/CardWithBack/CardWithBack';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

import './vehicle.scss';

import {
  editVehicleSaga,
  getVehicleDetailByIdSaga,
  resetVehicleDetail,
} from '../../../store/actions';

const EditVehicleType = props => {
  const editId = props.match.params.id;
  const { errorMsg, vehicle } = useSelector(state => state.vehicleTypes);
  //   const [photoURL, setPhotoURL] = useState(null);
  const [photoURL, setPhotoURL] = useState(
    editId && vehicle.photo ? vehicle.photo : null,
  );
  const dispatch = useDispatch();
  const { open } = useSelector(state => state.modal);

  const tableFilter = {
    columnName: 'name',
    orderBy: 'asc',
    search: '',
    skip: 0,
    limit: 10,
  };

  const validationSchema = Yup.object({
    name: Yup.string().trim().required('Vehicle Type is Required'),
    minimumFare: Yup.number()
      .typeError('Enter Number Only')
      .required('Minimum Fare is Required'),
    perKmFare: Yup.number()
      .typeError('Enter Number Only')
      .required('Per Kilometer Fare is Required'),
    photo: Yup.string(),
  });

  const submitButtonHandler = values => {
    values.id = editId;
    dispatch(
      editVehicleSaga({
        formValues: values,
        tableFilter,
      }),
    );
    console.log(values);
  };

  useEffect(() => {
    if (editId) {
      dispatch(getVehicleDetailByIdSaga(editId));
    }
    return () => {
      dispatch(resetVehicleDetail());
    };
  }, []);

  useEffect(() => {
    if (vehicle.photo) {
      setPhotoURL(vehicle.photo);
    }
  }, [vehicle.photo]);
  return (
    <>
      <CardWithBack
        pageTitle={
          props.history.location.pathname.includes('add')
            ? 'Add Vehicle'
            : 'Edit Vehicle'
        }
        dataLoadeed={typeof 1 === typeof 2}
        errorMsgAddPromoCode=""
      >
        <Formik
          enableReinitialize
          initialValues={{
            name: editId && vehicle.type ? vehicle.type : '',
            minimumFare: editId && vehicle.minFare ? vehicle.minFare : '',
            perKmFare: editId && vehicle.feePerKM ? vehicle.feePerKM : '',
            photo: editId && vehicle.photo ? vehicle.photo : '',
          }}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            submitButtonHandler(values);
            setSubmitting(false);
          }}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            // isSubmitting,
          }) => (
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-xs-12 col-lg-12 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Vehicle Type:</h4>
                    <input
                      type="text"
                      name="name"
                      placeholder="Enter Vehicle Type"
                      className="form-control"
                      value={values.name}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.name && touched.name && errors.name}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field"> Minimum Fare (KHR)</h4>
                    <input
                      type="text"
                      name="minimumFare"
                      placeholder="Enter Minimum Fare"
                      className="form-control"
                      value={values.minimumFare}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.minimumFare &&
                        touched.minimumFare &&
                        errors.minimumFare}
                    </div>
                  </div>
                </div>
                <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                  <div className="from-group text-left">
                    <h4 className="required-field">Per KiloMeter Fare : </h4>
                    <input
                      type="text"
                      name="perKmFare"
                      placeholder="Enter Per KiloMeter Fare"
                      className="form-control"
                      value={values.perKmFare}
                      // onChange={e => setFirstName(e.target.value.trim())}
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                    <div className="error-message">
                      {errors.perKmFare &&
                        touched.perKmFare &&
                        errors.perKmFare}
                    </div>
                  </div>
                </div>
                {/* FIle Uploading button styles */}
                {/* <div className="col-xs-12 col-lg-6 col-md-6 col-sm-6">
                <div className="form-group file-area">
                  <label htmlFor="images">
                    Images{' '}
                    <span>Your images should be at least 400x300 wide</span>
                  </label>
                  <input
                    type="file"
                    name="images"
                    id="images"
                    required="required"
                    multiple="multiple"
                  />
                  <div className="file-dummy">
                    <div className="success">
                      Great, your files are selected. Keep on.
                    </div>
                    <div className="default">Please select some files</div>
                  </div>
                </div>
              </div> */}

                <div className="col-xs-12 col-lg-3 col-md-6 col-sm-3">
                  <div className="from-group text-left">
                    <h4 className="required-field">Upload Vehicle Image: </h4>
                    <input
                      type="file"
                      name="photo"
                      id="photo1"
                      accept="image/x-png ,image/png, image/jpeg, images/gif"
                      onChange={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue('photo', e.currentTarget.files[0]);
                              setPhotoURL(URL.createObjectURL(files[0]));
                            }
                          }
                        }
                      }}
                      // onChange={handleChange}
                      onBlur={e => {
                        const { files } = e.target;
                        if (files[0]) {
                          if (files[0].type.includes('image')) {
                            const filename = files[0].name;
                            const fileExtension = filename.substr(
                              filename.lastIndexOf('.') + 1,
                            );
                            if (
                              fileExtension.toLowerCase() === 'png' ||
                              fileExtension.toLowerCase() === 'jpg' ||
                              fileExtension.toLowerCase() === 'gif' ||
                              fileExtension.toLowerCase() === 'jpeg'
                            ) {
                              setFieldValue('photo', e.currentTarget.files[0]);
                              setPhotoURL(URL.createObjectURL(files[0]));
                            }
                          }
                        }
                      }}
                    />

                    {photoURL && (
                      <img
                        className="photo-preview"
                        alt="dishImage"
                        style={{ width: '50%' }}
                        src={photoURL}
                      />
                    )}
                    <div className="error-message">
                      {errors.photo && touched.photo && errors.photo}
                    </div>
                  </div>
                </div>
              </div>
              <div className="form-group">
                <button
                  type="submit"
                  className="btn btn-primary m-l-sm m-b-md"
                  style={{ marginTop: '20px' }}
                >
                  Save
                </button>
              </div>
              <div className="error-message">{errorMsg}</div>
            </form>
          )}
        </Formik>
      </CardWithBack>
      {open && <AlertMessageModal />}
    </>
  );
};

export default SetTokenHeader(withRouter(EditVehicleType), axios);
